.contact-form-container {
    width: 100%;
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    
  }
  
  h2 {
    text-align: center;
    margin-bottom: 20px;
    color: #fcf1f1;
    font-family: Bell MT;
  }
  
  .form-group {
    margin-bottom: 15px;
  }
  
  .form-group label {
    display: block;
    margin-bottom: 5px;
    font-weight: bold;
  }
  
  .form-group input,
  .form-group textarea {
    width: 100%;
    padding: 10px;
    border: 1px solid #0a0a0a;
    border-radius: 5px;
    color: black;
  }
  
  .form-group button {
    width: 100%;
    padding: 10px;
    background-color: #28a745;
    color: rgb(255, 254, 254);
    border: none;
    border-radius: 5px;
    font-size: 16px;
    cursor: pointer;
  }
  
  .form-group button:hover {
    background-color: #218838;
  }
  #contact1{
    margin-left: 8.2cm;
    margin-top: 1.5cm;
    font-size: 18px;
    font-family: MT Black;
  }
  #contact2{
    margin-left: 6.9cm;
    font-family: MT Black;
  }
  #contact3{
    margin-left: 5.8cm;
    font-size: 1.2cm;
    font-family: Broadway;
  }
  #contact4{
    margin-left: 6.5cm;
    margin-bottom: 3cm;
    
  }
  
  