html{
  scroll-behavior: smooth;
  
}

body{
  margin: 0px !important;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  flex-wrap: wrap;
}

/* width */
::-webkit-scrollbar {
  width: 4px;
  height: 80px;
}

/* Track */
::-webkit-scrollbar-track {
  background: rgb(27,2,32);
  background: linear-gradient(90deg, rgba(27,2,32,1) 0%, rgba(46,12,57,1) 21%, rgba(94,8,113,1) 51%, rgba(27,2,32,1) 82%);
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: rgb(27,2,32);
  background: linear-gradient(90deg, rgba(27,2,32,1) 0%, rgba(46,12,57,1) 21%, rgba(94,8,113,1) 51%, rgba(27,2,32,1) 82%);
  border-radius: 1px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgb(27,2,32);
  background: linear-gradient(90deg, rgba(27,2,32,1) 0%, rgba(46,12,57,1) 21%, rgba(94,8,113,1) 51%, rgba(27,2,32,1) 82%);
}
   @media only screen and (max-width: 700px){
    body{
      height: 100%;
      width: 100%;
      flex-wrap: wrap;
      padding: auto;
      border-radius: 1px;
    }
  }